<template>
  <div class="vedio-wrapper">
    <!-- <img class="vedio" src="/img/temp.jpg" alt=""> -->
    <!-- <img class="vedio" src="http://192.168.3.44:5000/video_feed" alt=""> -->
    <canvas ref="canvas" class="canvas" width="640" height="480"></canvas>
    <div class="draw-area">
    </div>
  </div>
</template>

<script>
export default ({
  name: 'VedioMod',
  mounted () {
    setInterval(() => {
      var canvas = this.$refs.canvas
      var context = canvas.getContext('2d')
      var image = new Image()
      image.crossOrigin = 'Anonymous'
      image.src = 'http://' + location.hostname + ':5000/video_feed'

      image.onload = () => {
        // 将图片按像素写入画布
        // console.log(image.height)
        context.drawImage(image, 0, 0, 640, 480)
        // 读取图片某位置像素信息(x,y为需要读取的像素位置)
        // var imageData = context.getImageData(50, 50, 1, 1).data
        // console.log(this.rgbToHsv([imageData[0], imageData[1], imageData[2]]))
      }
    }, 1000 / 24)

    // var canvas = this.$refs.canvas
    // var context = canvas.getContext('2d')
    // var image = new Image()
    // image.crossOrigin = 'Anonymous'
    // image.src = 'http://192.168.3.44:5000/video_feed'

    // setInterval(() => {
    //   // 将图片按像素写入画布
    //   context.drawImage(image, 0, 0, this.width, this.height)
    //   // 读取图片某位置像素信息(x,y为需要读取的像素位置)
    //   var imageData = context.getImageData(50, 50, 1, 1).data
    //   console.log('=========================================')
    //   console.log(imageData)
    // }, 1000 / 24)
  },
  methods: {
    rgbToHsv (arr) {
      var h = 0; var s = 0; var v = 0
      var r = arr[0]; var g = arr[1]; var b = arr[2]
      arr.sort(function (a, b) {
        return a - b
      })
      var max = arr[2]
      var min = arr[0]
      v = max / 255
      if (max === 0) {
        s = 0
      } else {
        s = 1 - (min / max)
      }
      if (max === min) {
        h = 0// 事实上，max===min的时候，h无论为多少都无所谓
      } else if (max === r && g >= b) {
        h = 60 * ((g - b) / (max - min)) + 0
      } else if (max === r && g < b) {
        h = 60 * ((g - b) / (max - min)) + 360
      } else if (max === g) {
        h = 60 * ((b - r) / (max - min)) + 120
      } else if (max === b) {
        h = 60 * ((r - g) / (max - min)) + 240
      }
      h = parseInt(h)
      s = parseInt(s * 100)
      v = parseInt(v * 100)
      return [h, s, v]
    }
  }
})
</script>

<style lang="stylus" scoped>
  @import '~@css/mixins.styl'
  @import '~@css/varibles.styl'
