<template>
  <div></div>
</template>
<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'HandleWebsocket',
  data () {
    return {
      websock: null
    }
  },
  computed: {
    ...mapState(['wsContent'])
  },
  created () {
    this.initWebSocket()
  },
  destroyed () {
    this.websock.close() // 离开路由之后断开websocket连接
  },
  methods: {
    initWebSocket () { // 初始化weosocket
      console.log('now location -- ' + location.hostname)
      const wsuri = 'ws://' + location.hostname + ':8888'
      // const wsuri = 'ws://' + '192.168.3.91' + ':8888'
      this.websock = new WebSocket(wsuri)
      this.websock.onmessage = this.websocketonmessage
      this.websock.onopen = this.websocketonopen
      this.websock.onerror = this.websocketonerror
      this.websock.onclose = this.websocketclose
    },
    websocketonopen () { // 连接建立之后执行send方法发送数据
      // const actions = { admin: '123456' }
      // this.websocketsend(JSON.stringify(actions))
      this.websock.send('admin:123456')
    },
    websocketonerror () { // 连接建立失败重连
      this.initWebSocket()
    },
    websocketonmessage (e) { // 数据接收
      console.log('============ responseData ============')
      let redata = null
      try {
        redata = JSON.parse(e.data)
      } catch {
        redata = e.data
        console.log('response Data not JSON')
      }
      console.log(redata)
      this.changeWsResponse(redata)
    },
    websocketsend (Data) { // 数据发送
      if (Data) {
        console.log('============ sendingData ============')
        try {
          Data = JSON.stringify(Data)
        } catch (e) {
          console.log('can`t send with JSON')
        }
        console.log(Data)
        this.websock.send(Data)
        this.changeWsContent('')
      }
    },
    websocketclose (e) { // 关闭
      console.log('断开连接', e)
    },
    ...mapActions(['changeWsResponse']),
    ...mapActions(['changeWsContent'])
  },
  watch: {
    wsContent () {
      if (this.wsContent) {
        this.websocketsend(this.wsContent)
      }
    }
  }
}
</script>
